.loader {
  width: 12px;
  height: 12px;
  color: #3f56b4;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }

  25% {
